nav {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  background: white;
  font-weight: 600;
  font-family: 'Space Mono', monospace;
  position: sticky;
  top: 0;

  background-image: url(https://i.pinimg.com/originals/33/ef/8b/33ef8b9c0b902154a6cd4103a21275ef.jpg);
  background-size: cover;
  height: 150px;

}

.center {
  display: flex;
  justify-content: space-between;
}

#signup {
  border-radius: 100px;
  color: white;
  text-shadow: black 1px 1px;
  font-size: 30px;
  background-color: #df6870; 
}

#login {
  
  border-radius: 100px;
  color: #df6870;
  text-shadow: white 1px 1px;
  font-size: 30px;
  
}

.nav__authLinks{
  display: flex;
  align-items: center;
  color: white;
}



.nav__projectName {
  color: #e4f9ff;
  text-decoration: none;
  color: none;
}

.authLink {
  text-decoration: none;
  transition: color 0.2s ease-in-out;
   margin-left: 10px;
   padding: 20px;
   margin: 20px;
   text-shadow: white 1px 1px 1px;
   font-size: 20px;
}





#fa {
  display: flex;
  align-items: center;
}

#length {
  margin: 0;
  background-color: #df6870;
  border-radius: 100px;
  padding: 3px 7px;
  color: black;
}

.nav-logoutbtn {
  font-family: inherit;
  font-weight: inherit;
  margin-left: 1em;
  background-color: #5595ff;
  box-shadow: 0 0 5px 0 aliceblue;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #e3f2ff;
  transition: all 0.2s ease-in-out;
  height: 35px;
  width: 100px
}

.nav-logoutbtn:hover {
  transform: scale(1.05);
  background-color: #d13e3e;
}

Link {
  margin-left: 10px;
}

.snitch {
  margin-top: 30px;
  font-size: 50px;
  color: white;
  text-shadow: black 5px 5px 5px;
  
}

.snitch:hover {
  color: rgb(228, 228, 228);
  text-decoration: none;
}




.snitch:visited { text-decoration: none;  } 
.snitch:hover { text-decoration: none; }
.snitch:focus { text-decoration: none; }
.snitch:hover, a:active { text-decoration: none;  }
.snitch {
  text-decoration: none;
}

h1 a {
  text-decoration: none;
}


.wrapper{
  position: relative;
  }
  .wrapper span{
  position: absolute;
  top: -2px;
  right: -2px; 
  }